<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "dustlessRoom-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      dustlessForm: {
        LD1: "LD1", // 廠區
        Floor: "", // FAB 廠別
        SPONSOR: "", // 廠長 (負責人)
      },
      workerLoading1: false,
      workerList1: [], // 新增用的員工選單

      dustlessTableColumns: [
        { id: "index", label: "項次", width: "100" },
        { id: "locInfo", label: "廠區代號(備註)", width: "175" },
        { id: "location_Type_Description", label: "主區域", width: "125" },
        { id: "floor", label: "FAB廠別/Main Area", width: "225" },
        { id: "empInfo", label: "廠長(負責人)", width: "" },
      ],
      editDustless: false,
      editDustlessItem: {
        locInfo: "", // 廠區代號
        fabInfo: "", // 主區域
        Fab_Approve_Id: "", // FAB廠別
        Floor: "", //FAB廠別名稱
        Emp_No: "", // 廠長(負責人)
      },
      workerLoading2: false,
      workerList2: [], // 修改用的員工選單
    };
  },
  computed: {
    // 無塵室的廠區清單
    facList() {
      return this.$store.state.facList_all;
    },
    workerList() {
      return this.$store.state.signoff_workerList;
    },
    // 無塵室負責人資料 (All)
    dustless() {
      return this.$store.state.room_all;
    },
    // 無塵室負責人資料 (單頁)
    dustlessList() {
      return this.$store.state.room_allList;
    },
    fabList() {
      return this.$store.state.dustless_fabList;
    },
  },
  methods: {
    async qryDustless() {
      try {
        await this.$store.dispatch("getAllRoomList", {
          location_type: "1", // 1: 無塵室
          ld1: this.dustlessForm.LD1,
        });

        // this.$refs.dustlessForm.resetFields();
        this.$store.commit("CURRENT_PAGE_INIT");
      } catch (err) {
        console.log(err);
      }
    },
    // 按 enter 後蒐取員工清單，供新增廠長選用
    async workerSearchHandler1(evt) {
      this.workerLoading1 = true;
      if (evt.key == "Enter") {
        this.workerLoading1 = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getWorkerList", evt.target.value);
        this.workerList1 = this.workerList;
      }
    },
    async addDustless() {
      if (!this.dustlessForm.Floor || !this.dustlessForm.SPONSOR) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請輸入 FAB 廠別及負責人",
        });
      } else {
        try {
          await this.$store.dispatch("addDustless", this.dustlessForm);
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "新增成功",
          });

          // 更新該廠區的負責人清單 (呈現第一頁)
          // this.$refs.dustlessForm.resetFields();
          this.qryDustless();
        } catch (err) {
          console.log(err);
        }
      }
    },

    // 當沒有負責人時，才可刪除 FAB 廠別
    async delDustless(item) {
      try {
        await this.$store.dispatch(
          "delSingleRoom",
          item.fab_Approve_Id.toString()
        );

        // 更新該廠區的負責人清單 (呈現第一頁)
        // this.$refs.dustlessForm.resetFields();
        this.qryDustless();
      } catch (err) {
        console.log(err);
      }
    },
    async updHandler(item) {
      this.editDustlessItem.locInfo = item.locInfo;
      this.editDustlessItem.fabInfo = `${item.location_Type_Description}(${item.location_Type})`;
      this.editDustlessItem.Floor = item.floor;
      this.editDustlessItem.Fab_Approve_Id = item.fab_Approve_Id;
      this.editDustlessItem.Emp_No = item.sponsor;

      // 取得可選的 FAB 廠別清單
      await this.$store.dispatch("getDustlessFabList");
      this.workerLoading2 = [];
      this.editDustless = true;
    },
    // 按 enter 後蒐取員工清單，供修改廠長選用
    async workerSearchHandler2(evt) {
      this.workerLoading2 = true;
      if (evt.key == "Enter") {
        this.workerLoading2 = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getWorkerList", evt.target.value);
        this.workerList2 = this.workerList;
      }
    },
    async updDustless() {
      try {
        await this.$store.dispatch("updDustless", this.editDustlessItem);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "修改成功",
        });

        // 關閉視窗，並更新該廠區的負責人清單 (呈現第一頁)
        this.editDustless = false;
        this.qryDustless();
      } catch (err) {
        console.log(err);
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "room_all",
        paramsListName: "room_allList",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    try {
      // 清空先前的查詢結果
      this.$store.state.room_all = [];
      this.$store.state.room_allList = [];

      // 打 API 取得廠區資料
      await this.$store.dispatch("getAllFacList");
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./dustlessRoom.html"></template>
